}

.footer2 {
  height: 100px;
  color: white;
  background: green;
}

.footer-container {
  height: 100px;
  position: relative;
  text-align: center;
  font-size: 20px;
}

.footer-container div {
  padding: 2em;
}

.example-enter {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity .5s ease-in;
  transition: opacity .5s ease-in;
}
.example-leave {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity .5s ease-in;
  transition: opacity .5s ease-in;
}

